          <h4>La nova proposta d’ordenació es va començar a tramitar a mitjans del 2002, amb les aprovacions inicials de la modificació del Pla General Metropolità (PGM) i del Pla Parcial per l’Ajuntament de Gavà. Al mateix temps es produïen mobilitzacions per part de la ciutadania en contra del projecte.</h4>
