function getStepContent(step) {
  switch (step) {
    case 0:
      return fase1();
    case 1:
      return fase2();
    case 2:
      return fase3();
    case 3:
      return fase4();
    case 4:
      return fase5();
    case 5:
      return fase6();
    default:
      return 'Pendent';
  }
}

function getSteps() {
  return [
    <Typography variant="h6">1976-1988: Acumulació de terrenys</Typography>,
    <Typography variant="h6">1988-1993: Primer plà d'Urbanització: Pla de Caralt</Typography>,
    <Typography variant="h6">1994-2000: Redefinició del projecte</Typography>,
    <Typography variant="h6">2001-2006: Aprobació del Plà de Ponent</Typography>,
    <Typography variant="h6">2007-2016: Crisis: l'Estat rescata la banca </Typography>,
    <Typography variant="h6">2017-2020: Fem fora els fons voltors </Typography>

  ];
}

export default function VerticalLinearStepper() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = (i) => {
    console.log(i)
    setActiveStep(i + 1);
  };

  const handleBack = (i) => {
    setActiveStep(i - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  const handleGo = (event,i) => {
    setActiveStep(i);
  };
  return (
    <div className={classes.root}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
          <Step key={label} onClick={(e) => { e.stopPropagation(); handleGo(e,index)}} >
            <StepLabel className={ index <= activeStep ? null : classes.over}>{label}</StepLabel>
            <StepContent>
              <Typography>{getStepContent(index)}</Typography>
              <div className={classes.actionsContainer}>
                <div>
                  <Button
                    disabled={activeStep === 0}
                    color="primary"
                    size="small"
                    onClick={(e) => {e.stopPropagation();handleBack(index)}}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? null : 'Anterior '}
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    size="small"
                    onClick={(e) => { e.stopPropagation();handleNext(index)}}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? 'Continuarà' : 'Seguent'}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography color="primary" align="center">Ara depen de nosaltres aturar-ho</Typography>

        </Paper>
      )}
    </div>
  );
}
